.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 500px;
    border: 1px solid black;
    margin: 1em auto 2em;
    padding: 0.5em;
    border-radius: 3px;
    background-color: #d8d8d8;
    box-shadow: 3px 3px 7px rgb(0 0 0 / 90%);
}